<template>
  <v-app id="inspire" class="app-main">
    <v-main class="bg-color">
      <v-container fluid class="fix-container">
        <v-row>
          <v-col
            cols="2"
          >
          <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              color="bg-color"
            >
            <v-row>
                <v-col cols="3">
                    <v-avatar
                      class="mt-2"
                      color="primary"
                      size="40"
                      tile
                    >
                      <span class="initials" v-if="!user.photo">{{initials}}</span>
                      <img :src="user.photo" v-if="user.photo">
                    </v-avatar>
                </v-col>
                <v-col class="mt-2">
                    <h3>Vugar Eyvazov</h3>
                    <p><a href="#" class="edit-link">View Profile</a></p>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-list
              dense
            >
              <v-list-item-group v-model="selectedItem" color="primary">
                <v-list-item 
                    v-for="(item, i) in items"
                    :key="i"
                    @click="goToPage(item)"
                >
                  <v-list-item-content>
                    <v-list-item-title>{{item}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
            </v-sheet>
          </div>
          </v-col>

          <v-col
            cols="7"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>

          <v-col
            cols="3"
          >
            <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
               <v-card>
                 <v-card-title>
                  <span> Announcements <v-btn icon><v-icon>mdi-reload</v-icon></v-btn></span>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Excepturi, quo vitae doloribus eaque neque illo ab libero iste hic veritatis eveniet consequuntur dolore ad, eum odio expedita officiis delectus! Quo.
                  </v-card-text>
              </v-card>
               <v-card class="mt-2">
                 <v-card-title>
                  <span> QUICK LINKS </span>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                     <v-list color="transparent">
                       <a href="https://ecm.socar.az" target="_blank">
                         <v-list-item
                           link
                           color="grey lighten-4"
                         >
                         <v-list-item-content>
                           <v-list-item-title >Socar</v-list-item-title>
                         </v-list-item-content>
                         </v-list-item>
                       </a>
                       <a href="https://ecm.caspianic.com" target="_blank">
                       <v-list-item
                         link
                         color="grey lighten-4"
                       >
                       <v-list-item-content>
                         <v-list-item-title >Caspianic</v-list-item-title>
                       </v-list-item-content>
                       </v-list-item>
                       </a>
                     </v-list>
                   </v-card-text>
                </v-card>
            </v-sheet>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 export default {
   name: 'LayoutDefault',
   data: () => ({ 
     items: ['Dashboard', 'News Feed', 'Admin', 'Instructions', 'HR', 'Chat', 'Report', 'CEO'],
     selectedItem: 0
   }),
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ])
   },
   methods: {
     ...mapActions(['initialLoad']),
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhoto'
     ]),
     ...mapActions('admin', [
       'getPollData'
     ]),
     goToPage(item){
       switch (item) {
          case 'News Feed':
           this.goTo('newsFeed');
           break;
          case 'Admin':
           this.goTo('admin');
           break;
          case 'Instructions':
           this.goTo('instructions');
           break;
          case 'HR':
            this.goTo('hr');
            break;
          case 'Chat':
            this.goTo('chat');
            break;
          case 'Report':
            this.goTo('report');
            break;
          case 'CEO':
            this.goTo('ceo');
            break;
         default:
           this.goTo('/')
           break;
       }
     },
     goTo(routeUrl){
         this.$router.push(routeUrl);
     }
   },
   created: async function(){
     await this.getMemberData();
     await this.getMemberPhoto();
     await this.getPollData();
     await this.initialLoad();
   },
 }
</script>

<style lang="scss">
    .fix-container{
      margin-left: 10px;
      margin-right: 10px;
    }
    #inspire{
      margin-top: 11px;
    }
    .bg-color{
        background-color: #f7f8f8 !important;
    }
    .initials {
        color: white
    }
    .edit-link{
        text-decoration: unset;
    }
    .app-main{
      font-family: Tahoma, Verdana, Geneva, Arial, Helvetica, sans-serif !important;
      font-size: 11px !important;
    }
    .pos-sticky{
      position: sticky;
      top: 10px
    }
</style>